const carouselFormatters = {
  getAltText: ({ data, index }: { data: { caption: string }; index: number }) =>
    data.caption || `${index + 1}. kp`,

  getNextLabel: ({
    currentIndex,
    views,
  }: {
    currentIndex: number;
    views: { [key: string]: any }[];
  }) => `Mutasd a(z) ${currentIndex + 2}. kpet a(z) ${views.length} kzl`,
  getPrevLabel: ({
    currentIndex,
    views,
  }: {
    currentIndex: number;
    views: { [key: string]: any }[];
  }) => `Mutasd a(z) ${currentIndex}. kpet a(z) ${views.length} kzl`,

  getNextTitle: () => 'Kvetkez (jobbra nyl)',
  getPrevTitle: () => 'Elz (balra nyl)',

  getCloseLabel: () => 'Bezrs (esc)',
  getFullscreenLabel: ({ isFullscreen }: { isFullscreen: boolean }) =>
    isFullscreen
      ? 'Kilps a teljes kpernys nzetbl (f)'
      : 'Teljes kpernyre vlts (f)',
};

export default carouselFormatters;
